.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 5;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal-readmore {
  transform: translate(-50%, -50%)scale(.9);
}

.modal-readmore {
  box-sizing: border-box;
  max-width: 280px;
  background-color: var(--white-text-color);
  border: 2px solid var(--accent);
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  padding: 26px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.close-button-readmore {
  width: 20px;
  height: 20px;
  background-color: var(--white-text-color);
  cursor: pointer;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.close-icon-readmore {
  width: 12px;
  height: 12px;
}

